'use strict';

const BASE_URL
  = 'https://mate-academy.github.io/phone-catalogue-static/api';
const PHONE_LIST_URL = `${BASE_URL}/phones.json`;
const DETAILS_URL = `${BASE_URL}/phones/`;
const body = document.body;
const ul = document.createElement('ul');

body.append(ul);

function getPhone() {
  return fetch(PHONE_LIST_URL)
    .then(response => response.json())
    .then(phones => {
      for (const item of phones) {
        ul.insertAdjacentHTML('beforeend', `<li data-id="${item.id}">
          ${item.name}
          </li>`);
      }

      return phones;
    })
    .then(phones => {
      const phonesId = phones.map(phone => phone.id);

      getPhonesDetails(phonesId);

      return phones;
    })
    .catch(error =>
      setTimeout(() => new Error(error)), 5000);
};

getPhone();

function getPhonesDetails(ids) {
  const promises = ids.map(id => fetch(`${DETAILS_URL}${id}.json`)
    .then(response => response.json()));

  return Promise.all(promises);
};
